import { Component, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { NavigationExtras } from '@angular/router';
import {Location} from "@angular/common"
import {FirebaseDataService} from "../services/firebase-data.service"


@Component({
  selector: 'app-contents',
  templateUrl: './contents.page.html',
  styleUrls: ['./contents.page.css'],
})
export class ContentsPage implements OnInit {
  language
  data
  errorMsg
// len=this.data.length 
  constructor(private activatedRoute:ActivatedRoute,private router: Router,public navCtrl: NavController,private location:Location,private fds:FirebaseDataService) { }
//   navigationExtras: NavigationExtras = {
//     queryParams: {
//         data: JSON.stringify(this.data),
        
//     }
// };
  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(params=>{
      this.language=params.type})
      switch(this.language){
        case "C Programming":{
          fetch('../../assets/data/cdata.json').then(res=>res.json()).then(json=>{this.data=json})
          
          break;
        }
        case "C++ Programming":{
          fetch('../../assets/data/c++data.json').then(res=>res.json()).then(json=>{this.data=json})
          break;
        }
        case "Java Programming":{
          fetch('../../assets/data/javadata.json').then(res=>res.json()).then(json=>{this.data=json})
          break;
        }
        case "Python Programming":{
        //  fetch('../../assets/data/pythondata.json').then(res=>res.json()).then(json=>{this.data=json})
        this.fds.getPythonData()
        .subscribe((data)=> {
                            this.data=data},
                   (error)=>{
                     console.log(error)
                      this.errorMsg=error})
        break;
        }
      }

      console.log(this.language)

      console.log(this.data)

      // console.log(this.len)
    

  }

  details(c){
    // this.navCtrl.navigateForward(['/details'], true, this.navigationExtras);
    // this.navCtrl.push('DetailPage', {data: c});

    this.router.navigate(['/details'],{ queryParams: {
      value : JSON.stringify(c) ,
      language:this.language,

  data:JSON.stringify(this.data)
    },
      });
  }

  goBack() {
    this.location.back();
  }
 
}
